import React from "react";
import { Link as DomLink } from "react-router-dom";

export interface LinkProps {
  to?: string;
  label: string;
  type: string;
}

function Link({ to = "/", label = "Link", type = "base" }: LinkProps) {
  return (
    <div className={`link link-${type}`}>
      <DomLink to={to}>{label}</DomLink>
    </div>
  );
}

export default Link;
